import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';

/**
 * Generated class for the HomePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-home',
  templateUrl: 'home.html',
})
export class HomePage {

  userData = { Username : "", Password: "" }

  // @ViewChild('myNav') nav: NavController
   

  // @ViewChild('username') uname;
  // @ViewChild('password') pass;


  constructor(public navCtrl: NavController, public navParams: NavParams,private alertCtrl:AlertController, private loadingCtrl: LoadingController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HomePage');
  }

  dologin()
  {
    if(this.userData.Username == "admin" && this.userData.Password == "password")
    {
        // let alert = this.alertCtrl.create({
        //   title: 'You will now be Logged In ....',
        //   subTitle: 'Logging in ----',
        //   buttons: ['OK']
        // });
        // alert.present();

          // Let's navigate from TabsPage to Page1
          this.goToPage2();
       
    }
    else{
      let alert = this.alertCtrl.create({
        title: 'Incorrect Details',
        subTitle: 'Please check the username and password!',
        buttons: ['OK']
      });
      alert.present();
    }
  }

  goToPage2() {
    let loading = this.loadingCtrl.create({
      spinner : 'hide',
      content: '<img src="../../assets/giphy.gif" alt="cant display gif"/>'
    });

    loading.present();

    setTimeout(() => {
      this.navCtrl.setRoot("IndexPage");
    }, 3000);

    setTimeout(() => {
      loading.dismiss();
    }, 3000);
  }

}



